<template>
  <div>
    <my-header></my-header>
    <div class="addressContainer">
      <div class="title">常用地址</div>
      <div class="row">
        <mt-cell title="所在地区">
          <mt-picker
            :visibleItemCount="1"
            :itemHeight="36"
            :slots="slots"
            valueKey="name"
            @change="handlerChange"
          ></mt-picker>
        </mt-cell>
      </div>
      <div class="row">
        <mt-cell title="详细地址">
          <mt-field
            :class="stateStreetno"
            type="text"
            placeholder="街道门牌信息"
            v-model="streetno"
            @blur.native.capture="checkStreetno"
          ></mt-field>
        </mt-cell>
      </div>
      <div class="row">
        <mt-button type="primary" size="large" @click="addSubmit"
          >保存</mt-button
        >
      </div>
    </div>
    <my-footer :tabActive="tabActive">></my-footer>
  </div>
</template>
<script>
import { mapState, mapMutations } from "vuex";
import addressArrayList from "../components/json/city2";
export default {
  data() {
    return {
      tabActive: "me",
      classErrProvince: "",
      classErrCity: "",
      classErrCountry: "",
      province: "", //省份
      city: "", //城市
      country: "", //区县
      streetno: "",
      stateStreetno: "",
      picker: {},
      slots: [
        {
          values: [{ name: "请选择省份", code: "" }],
          className: "selwidth",
        },
        { divider: true, content: "-" },
        {
          values: [{ name: "请选择城市", code: "" }],
          className: "selwidth",
        },
        { divider: true, content: "-" },
        {
          values: [{ name: "请选择区县", code: "" }],
          className: "selwidth",
        },
      ],
    };
  },
  computed: {
    ...mapState(["region"]),
  },
  methods: {
    ...mapMutations(["saveRegion"]),
    // ***选择地址***//
    handlerChange(picker, values) {
      // 城市开始
      this.picker = picker;
      var cityList = addressArrayList.filter((item) => {
        return item.code == values[0].code;
      });
      if (cityList.length > 0) {
        picker.setSlotValues(1, cityList[0].children);
        picker.setSlotValues(2, picker.getSlotValue(1).children); //区县
      } else {
        picker.setSlotValues(1, [{ name: "请选择城市", code: "" }]);
        picker.setSlotValues(2, [{ name: "请选择区县", code: "" }]); //区县
      }

      // 城市结束
      this.province = values[0].code;
      this.city = values[1].code;
      this.country = values[2].code;
    },
    ///// ***初始化下拉菜单***//
    initialAddressSelect(slotIndex, arrayFilter, selectedValue) {
      var selected = arrayFilter.filter((item) => {
        return item.code == selectedValue;
      });
      if (selected.length > 0) {
        this.picker.setSlotValue(slotIndex, selected[0]);
      }
      return selected;
    },
    initialData() {
      // 从数据库读取初始值，并初始化地区---开始

      // 120000___120100___120118  天津 --天津--静海区
      // 130000___130800___130802  河北--承德--双桥
      // this.province = 130000;
      // this.city = 130800;
      // this.country = 130802;

      // this.province = this.$store.state.region[0];
      // this.city = this.$store.state.region[1];
      // this.country = this.$store.state.region[2];
      // this.streetno = this.$store.state.region[3];
      this.province = this.region[0];
      this.city = this.region[1];
      this.country = this.region[2];
      this.streetno = this.region[3];

      // 初始化省份开始
      var arrSelectedProvince = this.initialAddressSelect(
        0,
        addressArrayList,
        this.province
      );
      // 初始化省份结束

      // 初始化城市开始
      var arrSelectedCity = this.initialAddressSelect(
        1,
        arrSelectedProvince.length > 0 ? arrSelectedProvince[0].children : [],
        this.city
      );
      // 初始化城市结束

      // 初始化区县开始
      this.initialAddressSelect(
        2,
        arrSelectedCity.length > 0 ? arrSelectedCity[0].children : [],
        this.country
      );
      // 初始化区县结束

      // 从数据库读取初始值，并初始化地区---结束
    },
    ///// ***检测所在地区***//
    checkaddress() {
      if (this.province == "") {
        this.slots[0].className = "selwidth error";
        return false;
      }
      if (this.city == "") {
        this.slots[2].className = "selwidth error";
        return false;
      }
      if (this.country == "") {
        this.slots[4].className = "selwidth error";
        return false;
      }
      this.slots[0].className = "selwidth";
      this.slots[2].className = "selwidth";
      this.slots[4].className = "selwidth";
      return true;
    },
    ///// ***检测详细地址***//
    checkStreetno() {
      if (this.streetno == "") {
        this.stateStreetno = "error";
        return false;
      }
      this.stateStreetno = "";
      return true;
    },
    // ***提交表单**///
    addSubmit() {
      if (this.checkaddress() && this.checkStreetno()) {
        this.saveRegion([
          this.province,
          this.city,
          this.country,
          this.streetno,
        ]);
        // this.$store.commit("saveRegion", [
        //   this.province,
        //   this.city,
        //   this.country,
        //   this.streetno,
        // ]);
        this.$toast("保存成功!");
        // var data = {
        //   province: this.province, //省份
        //   city: this.city, //城市
        //   country: this.country, //区县
        //   streetno: this.streetno,
        // };
        // this.axios
        //   .post("", this.qs.stringify(data))
        //   .then((result) => {})
        //   .catch((ex) => {});
      }
    },
  },
  created() {
    // 初始化省份开始
    addressArrayList.forEach((element) => {
      this.slots[0].values.push(element);
    });
    // 初始化省份结束
  },
  mounted() {
    this.initialData();
  },
};
</script>
<style>
.selwidth {
  width: 6rem;
}
.addressContainer {
  background-color: #fff;
  padding-bottom: 2rem;
}
.addressContainer .row {
  text-align: center;
  font-size: 1.304rem;
}
.addressContainer .row p {
  line-height: 3.5rem;
}
.addressContainer .row > .mint-cell,
.addressContainer .row p {
  min-height: 3.5rem;
  margin: 0 1.3rem;
  border-bottom: 0.1rem solid #dad8d8;
}

.addressContainer .row:last-child {
  width: 90%;
  margin: 1rem auto;
}
.picker-center-highlight:after,
.picker-center-highlight:before {
  height: 0;
}
</style>